  align-items: center;
  min-width: 212px;
  border-radius: 4px;
  box-sizing: border-box;
}

.btn:hover, a.btn:hover {
  background-color: #3162CC;
  transition: background-color 0.3s linear 0s;
  text-decoration: none;
}

