}

.btn.lilac:hover, a.btn.lilac:hover {
  background-color: #7561EB;
}

.btn.green, a.btn.green {
  background-color: #34C759;
}

.btn.green:hover, a.btn.green:hover {
  background-color: #178700;
}

.btn.dark-green, a.btn.dark-green {
  background-color: #34C759;
}

.btn.dark-green:hover, a.btn.dark-green:hover {
  background-color: #1BA100;
}

.btn.orange, a.btn.orange {
  background-color: #FCC150;
}

.btn.orange:hover, a.btn.orange:hover {
  background-color: #FCC150;
}

.btn.transparent, a.btn.transparent {
  background-color: rgba(255, 255, 255, 0.5);
}

.btn.transparent:hover, a.btn.transparent:hover {
  background-color: rgba(255, 255, 255, 0.8);
}

.btn.white, a.btn.white {
  background-color: transparent;
  border: 1px solid #FFFFFF;
}

.btn.white:hover, a.btn.white:hover {
