.line-title {
  font-weight: bold;
  margin: 0px;
  padding: 0px;
  font-size: 32px;
  line-height: 40px;
  color: #2E3B4D;
  position: relative;
  padding-left: 28px;
  min-height: 58px;
  display: flex;
  justify-content: center;
  box-sizing: border-box;
  flex-direction: column;
}

.line-title .l-title {
  margin: 0px;
  padding: 0px;
  font-size: 32px;
  line-height: 40px;
}

@media (max-width: 1311px) {
  .line-title .l-title {
    font-size: 38px;
    line-height: 46px;
  }
}

.line-title .l-subtitle {
  font-size: 16px;
  line-height: 24px;
  color: #2E3B4D;
  font-weight: 400;
  margin-bottom: 9px;
}

@media (max-width: 1311px) {
  .line-title {
    font-size: 38px;
    line-height: 46px;
    min-height: 0px;
    padding: 0px;
    padding-bottom: 20px;
    text-align: center;
    margin: 0 auto;
    width: auto;
    align-items: center;
  }
}

.line-title.double {
  min-height: 88px;
}

@media (max-width: 1311px) {
  .line-title.double {
    padding-bottom: 0px;
    min-height: 0px;
  }
  .line-title.double:after {
    display: none;
  }
  .line-title.double .l-subtitle {
    position: relative;
    margin-top: 35px;
  }
  .line-title.double .l-subtitle:before {
    content: '';
    position: absolute;
    width: 145px;
    height: 4px;
    top: -25px;
    left: calc(50% - 72.5px);
    background-color: #3E74E7;
  }
  .line-title.double.blue .l-subtitle:before {
    background-color: #0697DD;
  }
  .line-title.double.red .l-subtitle:before {
    background-color: #BF0000;
  }
  .line-title.double.lilac .l-subtitle:before {
    background-color: #7561EB;
  }
  .line-title.double.green .l-subtitle:before {
    background-color: #178700;
  }
  .line-title.double.orange .l-subtitle:before {
    background-color: #FCC150;
  }
  .line-title.double.transparent .l-subtitle:before {
    background-color: rgba(255, 255, 255, 0.5);
  }
}

@media (max-width: 767px) {
  .line-title.double .l-subtitle {
    max-width: 320px;
  }
}

.line-title:after {
  content: '';
  position: absolute;
  width: 6px;
  height: 100%;
  background-color: #3E74E7;
  left: 0px;
  top: 0px;
}

@media (max-width: 1311px) {
  .line-title:after {
    width: 145px;
    height: 4px;
    bottom: 0px;
    left: calc(50% - 72.5px);
    top: auto;
  }
}

.line-title.blue:after {
  background-color: #0697DD;
}

.line-title.red:after {
  background-color: #BF0000;
}

.line-title.lilac:after {
  background-color: #7561EB;
}

.line-title.green:after {
  background-color: #178700;
}

.line-title.orange:after {
  background-color: #FCC150;
}

.line-title.transparent:after {
  background-color: rgba(255, 255, 255, 0.5);
}

.line-title.white {
  color: white;
}

.line-title.white:after {
  background-color: white;
}

