}

.selectric-petrosoft-select .selectric-items li {
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: #727E8E;
  padding: 0px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  height: 44px;
  padding: 0 25px;
  position: relative;
  transition: all 0.3s linear 0s;
}

.selectric-petrosoft-select .selectric-items li:before {
  content: '';
  left: 0px;
  height: 100%;
  position: absolute;
  background: none;
  width: 5px;
  transition: all 0.3s linear 0s;
}

